@import "../mixin.scss";
.techs {
  width: 100%;
  min-height: 625px;
  padding: 100px 70px 40px 70px;
  box-sizing: border-box;
  background-color: #f5f5f5;
}
.techs__title {
  @include regularText;
  margin: 0 0 70px 0;
  font-size: 22px;
  line-height: 27px;
  letter-spacing: -0.04em;
  padding-bottom: 23px;
  border-bottom: 1px solid #000000;
}
.techs__description {
  @include regularText;
  text-align: center;
  margin: 0 0 70px 0;
  font-size: 50px;
  line-height: 58px;
  letter-spacing: -0.04em;
}
.techs__list {
  list-style: none;
  display: flex;
  gap: 10px;
  padding: 0;
  margin: 0 auto 70px auto;
  justify-content: center;
}
.techs__item {
  @include regularText;
  width: 90px;
  background: #e8e8e8;
  border-radius: 10px;
  font-size: 14px;
  line-height: 60px;
  text-align: center;
  letter-spacing: -0.04em;
  align-self: center;
  padding: 0 14px;
  box-sizing: border-box;
}
.techs__skills {
  display: flex;
  flex-direction: column;
  gap: 10px;
  margin: 0;
}
.techs__skill {
  @include mediumText;
  font-size: 14px;
  line-height: 22px;
}
.techs__skill::marker {
  color: #727272;
}
@media screen and (max-width: 960px) {
  .techs {
    padding: 50px 50px 30px 50px;
    min-height: 567px;
  }
  .techs__term {
    margin: 0 0 22px 0;
  }
  .techs__description {
    font-size: 30px;
    line-height: 36px;
    margin-bottom: 50px;
  }
  .techs__item {
    width: 84px;
    font-size: 12px;
    line-height: 57px;
    height: 57px;
  }
}

@media screen and (max-width: 540px) {
  .techs {
    padding: 70px 18px 18px 18px;
    min-height: 651px;
    background-color: #ffffff;
  }
  .techs__title {
    margin: 0 0 60px 0;
    font-size: 18px;
    line-height: 22px;
    padding-bottom: 28px;
  }
  .techs__term {
    font-size: 30px;
    line-height: 36px;
    margin-bottom: 24px;
  }
  .techs__list {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: auto;
    padding: 0 53px;
    box-sizing: border-box;
    gap: 10px;
  }
  .techs__item {
    width: auto;
  }
}
